hird, .half {\n  float: left;\n}\n.quarter {\n  width: 23%;\n  margin: 0 1%;\n}\n.third {\n  width: 31.3%;\n  margin: 0 1%;\n}\n.half {\n  width: 48%;\n  margin: 0 1%;\n}\n.hiplot h3 {\n  margin: 12px 0 9px;\n}\n.hiplot h3 small {\n  color: #888;\n  font-weight: normal;\n}\n.hiplot p {\n  margin: 0.6em 0;\n}\n.hiplot small {\n  line-height: 1.2em;\n}\n\n\n.renderedBar,\n.selectedBar {\n  width:0%;\n  font-weight: bold;\n  height: 100%;\n}\n.renderedBar {\n  background: #3d9aff;\n  border-right: 1px solid #666;\n}\n.selectedBar {\n  background: rgba(171, 171, 171, 0.5);\n  border-right: 1px solid #999;\n}\n.fillbar {\n  height: 2px;\n  line-height: 2px;\n  width: 100%;\n}\n.little-box {\n  width: 268px;\n  float: left;\n}\n.controls {\n  float: right;\n  height: 24px;\n  line-height: 24px;\n}\n.header button {\n  border-color: black !important;\n}\n.header button:disabled{\n  border: solid 1px transparent !important;\n}\n\n/* Scrollbars */\n\n::-webkit-scrollbar {\n  width: 10px;\n  height: 10px;\n}\n\n::-webkit-scrollbar-track {\n  background: #ddd;\n  border-radius: 12px;\n}\n\n::-webkit-scrollbar-thumb {\n  background: #b5b5b5;\n  border-radius: 12px;\n}\n\n\n.plotxy-graph-svg :global(.tick) line {\n  color: #9a9a9a26;\n}\n.distr-graph-svg :global(.tick) line {\n  color: #9a9a9a26;\n}\n\n.min-height-100 {\n  min-height: 100vh;\n}\n\n.horizontal-scrollable {\n  overflow-x: auto;\n}\n\n.colorBlock {\n  height: 10px;\n  width: 10px;\n  display: inline-block;\n}\n\n/* Tooltips when hovering column labels */\n.tooltipContainer {\n  display: inline-block;\n}\n\n.tooltipBot {\n  top: 100%;\n  left: 0%;\n}\n/* Tooltip text */\n.tooltipContainer .tooltiptext {\n  visibility: hidden;\n  background-color: black;\n  color: #fff;\n  text-align: center;\n  padding: 5px;\n  border-radius: 6px;\n}\n\n/* Show the tooltip text when you mouse over the tooltip container */\n.tooltipContainer:hover .tooltiptext {\n  visibility: visible;\n}\n\n\n/* Histogram */\n.histSelected line {\n  stroke: black;\n  stroke-width: 2;\n}\n.histAll rect {\n  fill: rgb(148, 103, 189);\n}\n\n\n.runsSelectionTextarea {\n  width:  100%;\n  height:  50px;\n  font-family:monospace;\n  font-size: 12pt;\n  resize: none;\n  overflow: hidden;\n}\n\n.hasFocus {\n  height: 25px !important;\n}\n\n.header {\n  border-bottom: 1px solid rgba(100,100,100,0.35);\n  background: #e2e2e2;\n  padding: 6px 24px 4px;\n  line-height: 24px;\n}\n.header h1 {\n  display: inline-block;\n  margin: 0px 14px 0 0;\n}\n.header button {\n  vertical-align: top;\n}\n\n.controlGroup {\n  margin-left: 5px;\n  margin-right: 5px;\n}\n\n\n/* dark theme */\n\n.dark .header {\n  background: #040404;\n  color: #f3f3f3;\n}\n\n.dark {\n  background: #131313;\n  color: #e3e3e3;\n}\n.dark a {\n  color: #5ae;\n}\n.dark .background {\n  fill: none;\n}\n.dark .brush rect.extent {\n  fill: rgba(100,100,100,0.15);\n  stroke: #ddd;\n}\n.dark ::-webkit-scrollbar-track {\n  background: #222;\n}\n.dark ::-webkit-scrollbar-thumb {\n  background: #444;\n}\n.dark .axis text {\n  fill: #f2f2f2;\n  text-shadow: 0 1px 0 #000, 1px 0 0 #000, 0 -1px 0 #000, -1px 0 0 #000;\n}\n.dark .axis text.label {\n  fill: #ddd;\n}\n.dark .axis line, .dark .axis path {\n  stroke: #777;\n}\n.dark .header button:enabled {\n  border-color: white !important;\n}\n.dark .histSelected line {\n  stroke: white;\n  stroke-width: 2;\n}\n.dark .histAll rect {\n  fill:rgb(99, 80, 117);\n}\n"],"sourceRoot":""}]);
